const state = () => ({
    filosofos: [],
    msgs: ["Monte seu time"],
    time: [],
    vida: 0,
    ataque: 0,
    vidaDragao: 0,
    ataqueDragao: 7,
    cabou: 0,
    isPorrada: false,
    isLoading: true
});

const actions = {
    getFilosofos({ commit }) {
        console.log('Loading room data...');
        fetch('data/filosofos.json')
            .then(res => res.json())
            .then(res => {
                console.log('Loaded.');
                commit('setFilosofos', res);
            });
    },
    mainLoop({ commit, state }) {
        setInterval(() => {
            if (state.isPorrada) {
                commit('porradaTick');
            }
        }, 1000)
    }
};

const mutations = {
    setFilosofos(state, filosofos) {
        state.filosofos = filosofos;
        state.isLoading = false;
    },
    newMsg(state, msg) {
        if (state.msgs.length == 20) {
            state.msgs.shift();
        }
        state.msgs.push(msg);
    },
    recomecar(state) {
        state.time = [];
        state.msgs = ["Monte seu time"];
        state.cabou = 0;
        state.vida = 0;
        state.ataque = 0;
        state.ataqueDragao = 6;
    },
    comprarFilosofo(state, filosofo) {
        let msg = filosofo.msg;
        let buff = filosofo.buff;
        let vida = filosofo.vida;
        let ataque = filosofo.ataque;
        if (state.time.length < 5) {
            state.time.push(filosofo);
            state.vida = state.vida + vida;
            state.ataque = state.ataque + ataque;
            this.commit("game/newMsg", msg);
            if (buff == 1) {
                state.ataque = state.ataque + 2;
            }
            if (buff == 2) {
                state.ataqueDragao = state.ataqueDragao - 1;
            }
        } else {
            this.commit("game/newMsg", "chega de filosofos! Chame o dragão");
        }
    },
    venderFilosofo(state, index) {
        let filosofo = state.time.filter((_, i) => i == index)[0];
        console.log(filosofo);
        console.log(filosofo["vida"]);
        state.time = state.time.filter((_, i) => i != index);
        console.log(state.time);
        state.vida = state.vida - filosofo.vida;
        state.ataque = state.ataque - filosofo.ataque;
    },
    chamarDragao(state) {
        state.vidaDragao = 50;
        state.msgs = [];
        state.isPorrada = true;
    },
    porradaTick(state) {
        this.commit("game/newMsg", "A porrada tá rolando!");
        state.vidaDragao = state.vidaDragao - state.ataque;
        state.vida = state.vida - state.ataqueDragao;
        if (state.vidaDragao <= 0 && state.vida <= 0) {
            state.vidaDragao = 0;
            this.commit("game/newMsg", "VOCE EMPATOU");
            state.cabou = 1;
            state.isPorrada = false;
        } else if (state.vidaDragao <= 0) {
            state.vidaDragao = 0;
            this.commit("game/newMsg", "VOCE VENCEU");
            state.cabou = 1;
            state.isPorrada = false;
        } else if (state.vida <= 0) {
            state.vidaDragao = 0;
            this.commit("game/newMsg", "VOCE PERDEU");
            state.cabou = 1;
            state.isPorrada = false;
        }
    }
};

const getters = {
    getMsg(state) {
        return state.msgs.slice(-4)
    },
};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
};