<template>
  <div id="time">
    <div v-if="time.length > 0">
      <p>Vida: {{ this.vida }}</p>
      <p>Ataque: {{ this.ataque }}</p>
      <div>
        <img
          v-bind:alt="c.hero"
          class="center-cropped"
          :src="getImgUrl(c.hero)"
          @click="venderFilosofo(index)"
          v-for="(c, index) in time"
          :key="index"
        />
      </div>
      <br /><br />
    </div>
    <div v-else>
      <p>Não tem ninguem no seu time ainda</p>
      <br /><br /><br /><br />
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "TeamCards",
  computed: {
    ...mapState("game", ["time", "vida", "ataque"]),
  },
  methods: {
    ...mapMutations("game", ["venderFilosofo"]),
    getImgUrl(pic) {
      return require("../assets/" + pic.substring(0, pic.length - 4) + ".png");
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.center-cropped {
  object-fit: contain;
  object-position: center;
  height: 100px;
  width: 100px;
  border: solid black 2px;
  padding: 2px;
  margin: 4px;
}
.center-cropped:hover {
  border-color: red;
}
</style>
