<!-- Use preprocessors via the lang attribute! e.g. <template lang="pug"> -->
<template>
  <div id="app">
    <h1>{{ title }}</h1>

    <div id="loading" v-if="isLoading">
      <p>Carregando...</p>
    </div>
    <div id="main" v-else>
      <ShopBox></ShopBox>
      <TeamCards></TeamCards>
      <LogBox></LogBox>
      <EnemyContainer></EnemyContainer>
    </div>
	
  </div>
</template>

<script>
import { mapState } from "vuex";
import TeamCards from "./components/TeamCards.vue";
import ShopBox from "./components/ShopBox.vue";
import LogBox from "./components/LogBox.vue";
import EnemyContainer from "./components/EnemyContainer.vue";

export default {
  components: {
    TeamCards,
    ShopBox,
	LogBox,
	EnemyContainer
  },
  data() {
    return {
      title: "Batalha dos políticos!",
    };
  },
  computed: {
    ...mapState("game", ["isLoading"]),
  },
  created() {
    this.$store.dispatch("game/getFilosofos");
    this.$store.dispatch("game/mainLoop");
  },
};
</script>

