<template>
  <div id="shop">
    <button
      v-for="(c, index) in filosofos"
      :key="index"
      :id="c.hero"
      @click="comprarFilosofo(c)"
    >
      Comprar {{ c.hero }}
    </button>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "ShopBox",
  computed: {
    ...mapState("game", ["filosofos"]),
  },
  methods: {
    ...mapMutations("game", ["comprarFilosofo"]),
  },
};
</script>

