<template>
  <div id="dragonLair">
    <div v-if="vidaDragao > 0">
      <p>DRAGAO!!!!!</p>
      <p>{{ this.vidaDragao }}</p>
      <img alt="graaaaurrrr" src="../assets/dragao.png" />
    </div>
    <div v-else>
      <div v-if="cabou == 0">
        <p>Daqui a pouco o dragão chega</p>
        <br />
        <button id="dragao" @click="chamarDragao()">CHAMA O DRAGÃO</button>
      </div>
      <div v-else>
        <button @click="recomecar()">recomeçar</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "ShopBox",
  computed: {
    ...mapState("game", ["vidaDragao", "cabou"]),
  },
  methods: {
    ...mapMutations("game", ["recomecar", "chamarDragao"]),
  },
};
</script>
